.member {
  display: flex;
  justify-content: flex-start;
  width: 100px;

  img {
    margin-right: -10px;
  }
}

.img-list {
  width: 80px;
  height: 60px;
  object-fit: cover;
  object-position: center;
}

.content {
  display: flex;
}
.image {
  flex: 1;
  min-height: 300px;
}
.text {
  flex: 2;
}
